import React from "react";
import HOC from "@/HOC/load-HOC.js";
const { AmdDocHOC, AshowDemoHOC } = HOC;
import doc from "zerod/components/ZeditSimpleFormHOC/doc.md";
import { ZeditSimpleFormHOC } from "zerod";
import getConfig from "./pageConfig";
export default AmdDocHOC(doc, {
	demo1: function() {
		const Com = ZeditSimpleFormHOC(getConfig("add"));
		const MyScript = AshowDemoHOC(<Com />, true);
		return <MyScript />;
	},
	demo2: function() {
		const Com = ZeditSimpleFormHOC(getConfig("update"));
		const MyScript = AshowDemoHOC(<Com detailId={1} />, true);
		return <MyScript />;
	},
});
